		methods:{
			// 项目类型单列
            showSinglePicker(e) {
				console.log(this.$refs.mpvuePicker.show())
				this.type = e.currentTarget.dataset.type
                this.pickerValueArray = this.pickerSingleArray
                this.mode = 'selector'
                this.deepLength = 1
                this.pickerValueDefault = [0]
                this.$refs.mpvuePicker.show()
				
