				let _this = this;
				console.log(JSON.stringify(e.label));
				if(this.type == "1"){
					_this.pickerText3 = e.label;
				}else if(this.type == "2"){
					_this.pickerText1 = e.label;
				}else if(this.type == "3"){
					_this.pickerText2 = e.label;
